<template>
  <div class="login">
    <h2>Connexion</h2>
    <div class="form-group">
      <div class="input-group">
        <input type="text" v-model="state.concernedApplication" id="concernedApplication" required="required"/>
        <label class="control-label" for="password">Projet source</label><i class="bar"></i>
      </div>
    </div>
    <div class="form-group">
      <div class="input-group">
        <input type="text" id="username" v-model="state.username" required="required"/>
        <label class="control-label" for="email">Nom d'utilisateur JIRA</label><i class="bar"></i>
      </div>
    </div>
    <div class="form-group">
      <div class="input-group">
        <input type="password" v-model="state.password" id="password" required="required"/>
        <label class="control-label" for="password">Mot de passe JIRA</label><i class="bar"></i>
      </div>
    </div>
    <div class="d-flex flex-column flex-lg-row align-items-center justify-content-between down-container">
      <button class="btn btn-info" v-on:click="connectUser()">
        Valider
      </button>
    </div>
  </div>
</template>

<script>
  import store from '../store/store'
  import swal from 'sweetalert2'

  export default {
    name: 'login',
    data () {
      return {
        state: {
          username: '',
          password: '',
          concernedApplication: ''
        },
        store: store
      }
    },
    methods: {
      connectUser () {
        this.store.actions.LOG_USER(this.state.username, this.state.password).then(() => this.checkTokenValidity())
      },
      checkTokenValidity () {
        if (!this.store.state.token) {
          return swal('Erreur', "Nom d'utilisateur ou mot de passe incorrect", 'error')
        }
        this.store.state.currentUser = this.state.username
        this.store.state.concernedApplication = this.state.concernedApplication
        window.location.href = '/#/dashboard'
      }
    }
  }
</script>
<style lang="scss">
  @import '../../../sass/variables';
  @import '../../../../node_modules/bootstrap/scss/mixins/breakpoints';
  @import '../../../../node_modules/bootstrap/scss/variables';

  .login {
    @include media-breakpoint-down(md) {
      width: 100%;
      padding-right: 2rem;
      padding-left: 2rem;
      .down-container {
        .link {
          margin-top: 2rem;
        }
      }
    }

    h2 {
      text-align: center;
    }
    width: 21.375rem;

    .down-container {
      margin-top: 3.125rem;
    }
  }
</style>



